#project-section {
	padding-top: 50px;
	width: 100%;
}

.image-a {
	height: 45%;
}

.image-a > img {
	height: 100%;
}

.projectCard {
	position: relative;
	height: 40rem;
	width: 25rem;
	background-color: #191919 !important;
	transition: all 1s ease;
	border: 1px solid white;
}
.projectCard:hover {
	transform: scale(1.05) rotate(2deg);
	box-shadow: -2px 9px 18px -7px rgba(255, 255, 255, 1);
}
.projectCard:hover .project-name,
.projectCard:hover .project-stack,
.projectCard:hover .project-description,
.projectCard:hover .button {
	transform: translateX(20px);
}

.card-body > * {
	color: white !important;
}

.project-section-head {
	font-size: 3rem;
}

.project-name {
	font-size: 2rem;
	text-decoration: none;
	transition: all 1s ease;
}

.project-name:hover {
	text-decoration: underline;
}

.project-stack-div {
	display: flex;
	flex-wrap: wrap;
}

.project-stack {
	font-size: 0.9rem;
	margin: 3px;
	padding: 8px;
	border-radius: 10px;
	border: 0.2px solid white;
	transition: all 1s ease;
}

.project-description {
	font-size: 1.2rem;
	transition: all 1s ease;
}

.links {
	position: absolute;
	bottom: 1rem;
}

.links * {
	margin: 0.5rem;
	width: 7.5rem;
	height: 2.5rem;
	font-size: 1.2rem;
	font-weight: 600;
}

.links * :hover {
	cursor: pointer;
}

.button {
	background-color: white !important;
	color: black !important;
	border: 2px solid black;
	width: fit-content;
	border-radius: 20px;
	transition: all 1s ease;
}

.button:hover {
	background-color: black !important;
	color: white !important;
	border: 2px solid white;
	text-decoration: none;
}
