body {
	background-color: black;
	color: #ffffff;
	margin: 0px;
	padding: 0px;
	overflow-x: hidden;
	font-family: "Poppins", sans-serif;
	font-weight: 400;
	font-style: normal;
}
* {
	box-sizing: border-box;
}

.anchor {
	display: block;
	height: 55px;
	visibility: hidden;
}

.App > section {
	margin: 0 2rem;
}

html,
body,
.app,
.App > * {
	width: 100% !important;
	/* min-width: 100vw; */
}
