#home-section {
	width: 100%;
	overflow-x: hidden;
}
.home-container {
	max-width: 95%;
}

.p-about {
	margin: 1rem;
}


.p-about > a > button > img {
	height: 1.7rem;
	width: 1.7rem;
	background-color: white;
	border-radius: 50%;
	border: 1px solid white;
}

button {
	background-color: white;
	color: black;
	border: 2px solid black;
	border-radius: 2rem;
	width: 13rem;
	margin-left: 1rem;
	transition: all 0.5s ease;
}

button:hover {
	background-color: black !important;
	color: white !important;
	border: 2px solid white;
}

.p-title {
	font-size: 5rem;
	text-align: center;
}

.p-about {
	font-size: 2rem;
	text-align: center;
}

.tech-stack {
	margin-top: 2rem;
}

.tech-stack-head {
	font-size: 2.5rem;
}
.stack {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.stack-item-div{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.stack-item-div-p{
	visibility: hidden;
	font-weight: 700;
}
.stack >  div > span  {
	background-color: white;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 7rem;
	width: 7rem;
	border-radius: 50%;
	margin: 1rem;
	transition: all 1s ease;
}
.stack > div > span > img {
	height: 5.5rem;
	width: 5.5rem;
}

.stack > div > span:hover {
	box-shadow: 0px 8px 18px 1px rgba(255, 255, 255, 0.87);
	-webkit-box-shadow: 0px 8px 18px 1px rgba(255, 255, 255, 0.87);
	-moz-box-shadow: 0px 8px 18px 1px rgba(255, 255, 255, 0.87);
	transform: rotate(360deg);
}

.stack > div > span:hover + .stack-item-div-p{
	visibility: visible;
}